#Main{min-height:90vh}#Main .router-spinner{width:100px;height:100px;margin-left:45%;position:relative}@media (max-width: 991px){#Main .router-spinner{top:-150px}}@media (max-width: 767px){#Main .router-spinner{margin-left:40%}}

nav{background:rgba(78,75,75,0.5);color:#fff;max-width:200px;margin:0 auto;position:relative;top:-170px}@media (max-width: 991px){nav{top:-300px}}nav a{color:#fff;font-size:25px;font-family:"Carter One",cursive;text-align:center;padding:10px 0;-webkit-transition:.3s ease-in;transition:.3s ease-in;border-bottom:2px solid rgba(170,170,170,0.2)}nav a:last-child{border-bottom:none}nav a:hover{color:rgba(255,68,0,0.7)}nav .active{background:rgba(255,0,0,0.25) !important}nav .active:hover{color:#fff}.sticky-navs{z-index:1000}.sticky-navs nav{min-width:100%;background:#4e4b4b;top:0;display:flex;justify-content:center}.sticky-navs nav a{border:none;font-size:15px}.sticky-navs nav .active{border-bottom:2px solid rgba(170,170,170,0.6)}

#Banner canvas{height:500px !important}#Banner .profile-block{margin:0 auto 30px;position:relative;top:-250px;max-width:500px;height:590px;background:rgba(78,75,75,0.5);box-shadow:0 0 10px 1px rgba(255,255,255,0.5)}#Banner .profile-block .img-wrap{display:flex;justify-content:center}#Banner .profile-block .img-wrap img{margin-top:35px;width:300px;height:300px;cursor:grab;cursor:-webkit-grab}#Banner .profile-block .text-block{text-align:center;color:#fff;margin-top:40px}#Banner .profile-block .text-block h2{font-family:"Open Sans",sans-serif;font-size:45px}#Banner .profile-block .text-block p{font-size:25px;font-family:"Open Sans Condensed",sans-serif}#Banner .profile-block .text-block a{display:inline-block;margin-top:10px;color:rgba(255,68,0,0.7);text-decoration:none;-webkit-transition:.5s ease-in;transition:.5s ease-in}#Banner .profile-block .text-block a:hover{color:#ccc}#Banner .profile-block .text-block ul{list-style:none;display:flex;justify-content:space-around !important;padding:0;margin-top:20px}#Banner .profile-block .text-block ul a{color:#999}#Banner .profile-block .text-block ul a svg{width:20px;height:20px}#Banner .profile-block .text-block ul a:hover{color:rgba(255,68,0,0.7)}@media (max-width: 991px){#Banner .profile-block{top:-400px;background:rgba(78,75,75,0.7)}}

