@import '../../../assets/styles/scss/data.scss';

#Banner{

  canvas{
    height: 500px !important;
  }

  .profile-block{
    margin: 0 auto 30px;
    position: relative;
    top: -250px;
    max-width: 500px;
    height: 590px;
    background: rgba(78, 75, 75, 0.5);
    box-shadow: 0 0 10px 1px rgba(255, 255, 255, 0.5);

    .img-wrap{
      display: flex;
      justify-content: center;

      img{
        margin-top: 35px;
        width: 300px;
        height: 300px;
        cursor: grab;
        cursor: -moz-grab;
        cursor: -webkit-grab;
      }
    }

    .text-block{
      text-align: center;
      color: #fff;
      margin-top: 40px;

      h2{
        font-family: $header-text;
        font-size: 45px;
      }

      p{
        font-size: 25px;
        font-family: $text-hi;
      }

      a{
        display: inline-block;
        margin-top: 10px;
        color: rgba(255, 68, 0, 0.7);
        text-decoration: none;
        transition: .5s ease-in;

        &:hover{
          color: #ccc;
        }
      }

      ul{
        list-style: none;
        display: flex;
        justify-content: space-around !important;
        padding: 0;
        margin-top: 20px;

        a{
          color: #999;
          svg{
            width: 20px;
            height: 20px;
          }

          &:hover{
            color: rgba(255, 68, 0, 0.7);
          }
        }
      }

    }

    @media ( max-width: 991px ) {
      top: -400px;
      background: rgba(78, 75, 75, 0.7);
    }
  }
}