@import '../../../assets/styles/scss/data.scss';

nav{
  background: rgba(78, 75, 75, 0.5);
  color: #fff;
  max-width: 200px;
  margin: 0 auto;
  position: relative;
  top: -170px;

  @media ( max-width: 991px ) {
    top: -300px;
  }


  a{
    color: #fff;
    font-size: 25px;
    font-family: $link-text;
    text-align: center;
    padding: 10px 0;
    transition: .3s ease-in;
    border-bottom: 2px solid rgba(170, 170, 170, 0.2);

    &:last-child{
      border-bottom: none;
    }

    &:hover{
      color: rgba(255, 68, 0, 0.7);
    }
  }

  .active{
    background: rgba(255, 0, 0, 0.25) !important;

    &:hover{
      color: #fff;
    }
  }
}

.sticky-navs{
  z-index: 1000;

  nav{
    min-width: 100%;
    background: rgba(78, 75, 75, 1);
    top: 0;
    display: flex;
    justify-content: center;

    a{
      border: none;
      font-size: 15px;
    }

    .active{
      border-bottom: 2px solid rgba(170, 170, 170, .6);
    }
  }
}